.search-form {
  background-color: #7f0e93ed;
  justify-content: center;
  gap: 20px;
  padding-top: 25px;
  padding-bottom: 25px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  text-decoration: none;
  display: flex;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  cursor: zoom-in;
}

.gallery__link {
  color: #141313;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  color: #d900ff;
}

.gallery__item {
  align-content: space-between;
  display: grid;
}

.round-button {
  visibility: hidden;
  color: azure;
  background-color: #f22525;
  border: none;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  padding: 10px;
  animation: 2s infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.for_upButton {
  justify-content: flex-end;
  margin-bottom: 20px;
  display: flex;
}

